import React, { useState, useEffect, useMemo } from 'react';
import {
  Box, Typography, TextField, InputAdornment, Button, Card, CardContent, 
  Avatar, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, 
  TablePagination, useTheme, Snackbar, Alert, LinearProgress, Grid, FormControl, 
  InputLabel, Select, MenuItem, Paper, Divider, Chip, Skeleton, Tooltip
} from '@mui/material';
import {
  CalendarToday as CalendarIcon,
  DateRange as DateRangeIcon,
  Assessment as AssessmentIcon, 
  Person as PersonIcon,
  People as PeopleIcon,
  Store as StoreIcon,
  Search as SearchIcon,
  FileDownload as FileDownloadIcon,
  FilterAlt as FilterIcon,
  LocalMall as ProductIcon,
  AttachMoney as MoneyIcon,
  WorkOutline as TeamIcon,
  TrendingUp as EfficiencyIcon
} from '@mui/icons-material';
import { collection, query, where, getDocs, orderBy, getDoc, doc, Timestamp } from 'firebase/firestore';
import { db } from '../../services/firebase';
import { useAuth } from '../../context/AuthContext';
import { getSubordinateIds, canViewManagerPerformanceReport } from '../../utils/roles';
import { useNavigate, useLocation, useParams } from 'react-router-dom';
import { format, parseISO, isValid } from 'date-fns';

// Helper function to format date
const formatDate = (timestamp) => {
  if (!timestamp) return 'N/A';
  
  if (timestamp && typeof timestamp.toDate === 'function') {
    return format(timestamp.toDate(), 'MMM d, yyyy');
  }
  
  if (timestamp instanceof Date) {
    return format(timestamp, 'MMM d, yyyy');
  }
  
  try {
    const date = new Date(timestamp);
    if (isValid(date)) {
      return format(date, 'MMM d, yyyy');
    }
  } catch (e) {
    console.error("Error parsing date:", e);
  }
  
  return 'Invalid Date';
};

// Get all subordinates recursively for a manager
const getAllSubordinatesRecursively = (managerId, allUsers) => {
  const directSubordinates = allUsers.filter(u => u.reportingToId === managerId);
  
  let allSubordinates = [...directSubordinates];
  const marketingStaff = [];
  
  // For each direct subordinate, get their subordinates
  directSubordinates.forEach(sub => {
    if (sub.role === 'MarketingStaff') {
      marketingStaff.push(sub);
    } else {
      // Recursively get deeper subordinates
      const deeperSubs = getAllSubordinatesRecursively(sub.id, allUsers);
      allSubordinates = [...allSubordinates, ...deeperSubs.allSubordinates];
      marketingStaff.push(...deeperSubs.marketingStaff);
    }
  });
  
  return { 
    allSubordinates, 
    marketingStaff
  };
};

// Get direct subordinates for a manager
const getDirectSubordinates = (managerId, allUsers) => {
  // Find direct subordinates
  const directSubordinates = allUsers.filter(u => u.reportingToId === managerId);
  
  // Count marketing staff under each direct subordinate
  const enrichedSubordinates = directSubordinates.map(sub => {
    // If this is a marketing staff, just return it
    if (sub.role === 'MarketingStaff') {
      return { 
        ...sub, 
        hasSubordinates: false,
        marketingStaffCount: 0 
      };
    }
    
    // Otherwise, count marketing staff under it
    const { marketingStaff } = getAllSubordinatesRecursively(sub.id, allUsers);
    return { 
      ...sub, 
      hasSubordinates: true,
      marketingStaffCount: marketingStaff.length 
    };
  });
  
  return enrichedSubordinates;
};

export default function ManagerPerformanceReport() {
  const theme = useTheme();
  const navigate = useNavigate();
  const location = useLocation();
  const { managerId } = useParams();
  const { userProfile, allUsers, loading: authLoading } = useAuth();
  
  // State
  const [loading, setLoading] = useState(false);
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');
  const [searchTerm, setSearchTerm] = useState('');
  const [reportData, setReportData] = useState([]);
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: 'success' });
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [currentManager, setCurrentManager] = useState(null);
  const [breadcrumbs, setBreadcrumbs] = useState([]);
  
  // Summary statistics
  const [totalManagers, setTotalManagers] = useState(0);
  const [totalMarketingStaff, setTotalMarketingStaff] = useState(0);
  const [totalVisits, setTotalVisits] = useState(0);
  const [totalSalesAmount, setTotalSalesAmount] = useState(0);

  // Initialize with current month date range
  useEffect(() => {
    const today = new Date();
    const firstDay = new Date(today.getFullYear(), today.getMonth(), 1);
    const lastDay = new Date(today.getFullYear(), today.getMonth() + 1, 0);
    
    setStartDate(firstDay.toISOString().split('T')[0]);
    setEndDate(lastDay.toISOString().split('T')[0]);
  }, []);

  // Effect to set current manager from URL or session storage
  useEffect(() => {
    const setupManager = async () => {
      if (managerId) {
        try {
          // Get manager from allUsers
          const manager = allUsers.find(u => u.id === managerId);
          if (manager) {
            setCurrentManager(manager);
            
            // Build breadcrumb path
            const breadcrumbPath = [];
            let currentId = manager.id;
            let current = manager;
            
            while (current) {
              breadcrumbPath.unshift(current);
              if (!current.reportingToId) break;
              current = allUsers.find(u => u.id === current.reportingToId);
            }
            
            setBreadcrumbs(breadcrumbPath);
          } else {
            // If can't find manager, check if we can view root level
            const rootLevelPermitted = checkRootLevelPermission();
            if (rootLevelPermitted) {
              // Set to root view
              setCurrentManager(null);
              setBreadcrumbs([]);
            } else {
              setSnackbar({
                open: true,
                message: 'Manager not found or you do not have permission to view',
                severity: 'error'
              });
              navigate('/sales/manager-performance');
            }
          }
        } catch (error) {
          console.error("Error setting up manager:", error);
        }
      } else {
        // Root level - check permission
        const rootLevelPermitted = checkRootLevelPermission();
        if (rootLevelPermitted) {
          setCurrentManager(null);
          setBreadcrumbs([]);
        } else {
          // Not permitted to view root level, try to find user's own position
          const myId = userProfile?.id;
          if (myId) {
            const me = allUsers.find(u => u.id === myId);
            if (me) {
              setCurrentManager(me);
              setBreadcrumbs([me]);
              navigate(`/sales/manager-performance/${myId}`);
            }
          }
        }
      }
    };
    
    if (allUsers && allUsers.length > 0 && userProfile) {
      setupManager();
    }
  }, [managerId, allUsers, userProfile, navigate]);

  // Check if user can view the root level
  const checkRootLevelPermission = () => {
    if (!userProfile) return false;
    
    // Admin and National Managers can view root level
    return ['Admin', 'NationalManager'].includes(userProfile.role);
  };
  
  // Get accessible managers based on current view
  const accessibleManagers = useMemo(() => {
    if (!userProfile || !allUsers) return [];
    console.log("Current view:", currentManager ? `Manager ${currentManager.id}` : "Root level");
    
    // If viewing a specific manager, return direct subordinates
    if (currentManager) {
      // Get direct subordinates of this manager
      const subordinates = getDirectSubordinates(currentManager.id, allUsers);
      console.log(`Found ${subordinates.length} direct subordinates for manager ${currentManager.id}`);
      return subordinates;
    }
    
    // Top level view logic based on role
    if (userProfile.role === 'Admin') {
      // For Admin at top level: show ONLY National Managers
      const managersList = allUsers.filter(u => 
        u.role === 'NationalManager' && u.active
      );
      
      // Enrich with subordinate info
      const enriched = managersList.map(manager => {
        const subResult = getAllSubordinatesRecursively(manager.id, allUsers);
        return {
          ...manager,
          hasSubordinates: subResult.allSubordinates.length > 0,
          marketingStaffCount: subResult.marketingStaff.length
        };
      });
      
      console.log(`Admin view: found ${enriched.length} National Managers`);
      return enriched;
    }

    // National Manager can see their direct subordinates
    if (userProfile.role === 'NationalManager') {
      const subs = getDirectSubordinates(userProfile.id, allUsers);
      console.log(`National Manager view: found ${subs.length} subordinates`);
      return subs;
    }

    // Area Officer can see their direct subordinates
    if (userProfile.role === 'AreaOfficer') {
      const subs = getDirectSubordinates(userProfile.id, allUsers);
      console.log(`Area Officer view: found ${subs.length} subordinates`);
      return subs;
    }

    // Supervisor can only see their direct subordinates (marketing staff)
    if (userProfile.role === 'Supervisor') {
      const subs = getDirectSubordinates(userProfile.id, allUsers);
      console.log(`Supervisor view: found ${subs.length} subordinates`);
      return subs;
    }

    console.log("No accessible managers found for role:", userProfile.role);
    return [];
  }, [userProfile, allUsers, currentManager]);

  // Simplify to directly use accessibleManagers without filtering by role
  const filteredManagers = useMemo(() => {
    if (!searchTerm.trim()) return accessibleManagers;
    
    // Apply search filter
    const searchLower = searchTerm.toLowerCase();
    return accessibleManagers.filter(manager => 
      manager.name?.toLowerCase().includes(searchLower)
    );
  }, [accessibleManagers, searchTerm]);

  // Auto-generate report after initialization with slight delay
  useEffect(() => {
    if (startDate && endDate && !loading && allUsers?.length > 0 && accessibleManagers?.length > 0) {
      // Add a short delay to ensure state updates are complete
      const timer = setTimeout(() => {
        console.log("Generating report with managers:", accessibleManagers.length);
        generateReport();
      }, 300);
      return () => clearTimeout(timer);
    }
  }, [startDate, endDate, allUsers, accessibleManagers]);

  // Generate the consolidated report
  const generateReport = async () => {
    if (!startDate || !endDate) {
      setSnackbar({
        open: true,
        message: 'Please select a date range',
        severity: 'warning'
      });
      return;
    }

    setLoading(true);
    
    try {
      // Create date range for query
      const startTimestamp = new Date(startDate + 'T00:00:00');
      const endTimestamp = new Date(endDate + 'T23:59:59');

      // Make sure we have managers to report on
      if (filteredManagers.length === 0) {
        console.log("No managers found with selected criteria", {
          userRole: userProfile?.role,
          currentManagerId: currentManager?.id
        });
        
        setReportData([]);
        setSnackbar({
          open: true,
          message: userProfile?.role === 'Admin' ? 
            'No managers found in the system. Please add staff with management roles first.' : 
            'No subordinate managers found for your account.',
          severity: 'info'
        });
        setLoading(false);
        return;
      }
      
      console.log(`Generating report for ${filteredManagers.length} managers`, filteredManagers);
      
      // Data structures to track team performance
      const managerMetrics = [];
      let globalTotalVisits = 0;
      let globalTotalMarketingStaff = 0;
      let globalTotalSalesAmount = 0;
      
      // For each manager, get performance metrics
      for (const manager of filteredManagers) {
        let marketingStaff = [];
        
        // If this is a marketing staff, just count them
        if (manager.role === 'MarketingStaff') {
          marketingStaff = [manager];
        } else {
          // Otherwise, get marketing staff under them
          const result = getAllSubordinatesRecursively(manager.id, allUsers);
          marketingStaff = result.marketingStaff;
        }
        
        // Always add the manager, even with zero metrics
        let teamVisits = 0;
        let uniqueRetailers = new Set();
        let teamTotalProducts = 0;
        let teamTotalSalesAmount = 0;
        
        // Only query visits if we have marketing staff
        if (marketingStaff.length > 0) {
          const staffIds = marketingStaff.map(staff => staff.id);
          globalTotalMarketingStaff += marketingStaff.length;
          
          // Query all visits for these marketing staff within date range
          const visitsQuery = query(
            collection(db, 'visits'),
            where('checkInTime', '>=', startTimestamp),
            where('checkInTime', '<=', endTimestamp),
            orderBy('checkInTime', 'desc')
          );
          
          const visitsSnapshot = await getDocs(visitsQuery);
          let visitsData = visitsSnapshot.docs.map(doc => ({
            id: doc.id,
            ...doc.data()
          }));
          
          // Filter for visits by this manager's marketing staff
          visitsData = visitsData.filter(visit => staffIds.includes(visit.userId));
          
          // Count metrics
          teamVisits = visitsData.length;
          globalTotalVisits += teamVisits;
          
          // Count unique retailers
          visitsData.forEach(visit => {
            if (visit.retailerId) uniqueRetailers.add(visit.retailerId);
          });
          
          // Fetch product rates for pricing
          const productRatesQuery = query(collection(db, 'productRates'));
          const productRatesSnapshot = await getDocs(productRatesQuery);
          const productRatesMap = {};
          
          productRatesSnapshot.forEach(doc => {
            const data = doc.data();
            if (data.productId && data.rate) {
              productRatesMap[data.productId] = parseFloat(data.rate);
            }
          });
          
          // Fetch inventory entries for pricing data
          const inventoryEntryIds = new Set();
          visitsData.forEach(visit => {
            if (visit.inventoryEntryId) {
              inventoryEntryIds.add(visit.inventoryEntryId);
            }
          });
          
          const inventoryEntriesMap = {};
          if (inventoryEntryIds.size > 0) {
            const inventoryPromises = Array.from(inventoryEntryIds).map(async (entryId) => {
              try {
                const entryDoc = await getDoc(doc(db, 'inventoryEntries', entryId));
                if (entryDoc.exists()) {
                  inventoryEntriesMap[entryId] = entryDoc.data();
                }
              } catch (error) {
                console.error(`Error fetching inventory entry ${entryId}:`, error);
              }
            });
            
            await Promise.all(inventoryPromises);
          }
          
          // Calculate products and sales amount
          for (const visit of visitsData) {
            if (Array.isArray(visit.products)) {
              for (const product of visit.products) {
                const count = parseInt(product.count) || 0;
                teamTotalProducts += count;
                
                // Try to find rate in different sources
                let rate = null;
                
                // 1. Check product itself
                if (product.rate) {
                  rate = parseFloat(product.rate);
                } 
                // 2. Check inventory entry
                else if (visit.inventoryEntryId && inventoryEntriesMap[visit.inventoryEntryId]) {
                  const inventoryEntry = inventoryEntriesMap[visit.inventoryEntryId];
                  const productEntry = inventoryEntry.productEntries?.find(pe => pe.productId === product.productId);
                  if (productEntry && productEntry.rate) {
                    rate = parseFloat(productEntry.rate);
                  }
                }
                // 3. Check global product rates
                if (rate === null && productRatesMap[product.productId]) {
                  rate = productRatesMap[product.productId];
                }
                
                // Calculate amount if we have a rate
                if (rate !== null) {
                  const saleAmount = count * rate;
                  teamTotalSalesAmount += saleAmount;
                }
              }
            }
          }
          
          globalTotalSalesAmount += teamTotalSalesAmount;
        }
        
        // Add manager metrics
        managerMetrics.push({
          id: manager.id,
          managerId: manager.id,
          managerName: manager.name || "Unknown",
          managerRole: manager.role,
          teamSize: marketingStaff.length,
          totalVisits: teamVisits,
          uniqueRetailers: uniqueRetailers.size,
          totalProducts: teamTotalProducts,
          totalSalesAmount: teamTotalSalesAmount,
          hasSubordinates: manager.hasSubordinates || false,
          // Efficiency metrics
          visitsPerStaff: marketingStaff.length ? (teamVisits / marketingStaff.length).toFixed(1) : 0,
          salesPerStaff: marketingStaff.length ? (teamTotalSalesAmount / marketingStaff.length).toFixed(2) : 0,
          salesPerVisit: teamVisits ? (teamTotalSalesAmount / teamVisits).toFixed(2) : 0
        });
      }
      
      // Update summary statistics
      setTotalManagers(managerMetrics.length);
      setTotalMarketingStaff(globalTotalMarketingStaff);
      setTotalVisits(globalTotalVisits);
      setTotalSalesAmount(globalTotalSalesAmount);
      
      // Sort by total sales amount (descending)
      managerMetrics.sort((a, b) => b.totalSalesAmount - a.totalSalesAmount);
      
      // Set report data
      setReportData(managerMetrics);
      
      // Show message if no data
      if (managerMetrics.length === 0) {
        setSnackbar({
          open: true,
          message: 'No staff performance data found',
          severity: 'info'
        });
      }
    } catch (error) {
      console.error("Error generating report:", error);
      setSnackbar({
        open: true,
        message: 'Error generating report: ' + error.message,
        severity: 'error'
      });
    } finally {
      setLoading(false);
    }
  };

  // Filter data based on search term
  const filteredData = useMemo(() => {
    if (!searchTerm.trim()) return reportData;
    
    const searchLower = searchTerm.toLowerCase();
    return reportData.filter(manager => 
      (manager.managerName?.toLowerCase().includes(searchLower) || manager.name?.toLowerCase().includes(searchLower))
    );
  }, [reportData, searchTerm]);

  // Pagination handlers
  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  // Display rows with pagination
  const displayedRows = filteredData.slice(
    page * rowsPerPage,
    page * rowsPerPage + rowsPerPage
  );

  // Export to CSV
  const exportToCSV = () => {
    if (reportData.length === 0) {
      setSnackbar({
        open: true,
        message: 'No data to export',
        severity: 'warning'
      });
      return;
    }
    
    // Define headers
    const headers = [
      'Manager Name',
      'Role',
      'Team Size',
      'Total Visits',
      'Unique Retailers',
      'Total Products',
      'Total Sales Amount',
      'Visits Per Staff',
      'Sales Per Staff',
      'Sales Per Visit'
    ];
    
    // Create rows
    const rows = reportData.map(manager => [
      manager.managerName,
      manager.managerRole,
      manager.teamSize,
      manager.totalVisits,
      manager.uniqueRetailers,
      manager.totalProducts,
      manager.totalSalesAmount.toFixed(2),
      manager.visitsPerStaff,
      manager.salesPerStaff,
      manager.salesPerVisit
    ]);
    
    // Create CSV content
    let csvContent = headers.join(',') + '\n';
    rows.forEach(row => {
      const processedRow = row.map(field => {
        // Quote fields with commas
        if (typeof field === 'string' && (field.includes(',') || field.includes('"'))) {
          return `"${field.replace(/"/g, '""')}"`;
        }
        return field;
      });
      csvContent += processedRow.join(',') + '\n';
    });
    
    // Add report title and date range
    csvContent = `Staff Performance Report\nDate Range: ${startDate} to ${endDate}\n\n` + csvContent;
    
    // Create and download the file
    const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.setAttribute('download', `manager_performance_${startDate}_${endDate}.csv`);
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  // Helper function to get role color
  const getRoleColor = (role) => {
    switch (role) {
      case 'NationalManager':
        return theme.palette.primary.main;
      case 'AreaOfficer':
        return theme.palette.info.main;
      case 'Supervisor':
        return theme.palette.warning.main;
      default:
        return theme.palette.grey[500];
    }
  };

  // Handle drilling down into a subordinate
  const handleDrillDown = (manager) => {
    console.log("Drilling down into manager:", manager);
    
    if ((manager.managerRole || manager.role) === 'MarketingStaff') {
      // For marketing staff, go to their detailed report
      navigate(`/sales/staff/${manager.id || manager.managerId}/details`);
    } else if (manager.hasSubordinates) {
      // For managers with subordinates, navigate to their subordinates view
      navigate(`/sales/manager-performance/${manager.id || manager.managerId}`);
    } else {
      // For managers without subordinates, show message
      setSnackbar({
        open: true,
        message: 'This manager has no subordinates to display',
        severity: 'info'
      });
    }
  };

  // Handle navigate up in the hierarchy
  const handleNavigateUp = (manager) => {
    if (manager && manager.id) {
      navigate(`/sales/manager-performance/${manager.id}`);
    } else {
      navigate(`/sales/manager-performance`);
    }
  };

  // Check if user has permission to view this report
  if (!canViewManagerPerformanceReport(userProfile)) {
    return (
      <Box sx={{ p: 4, textAlign: 'center' }}>
        <Typography variant="h6" color="error">
          You are not authorized to view staff performance reports.
        </Typography>
      </Box>
    );
  }

  return (
    <Box>
      <Card sx={{ borderRadius: 2, boxShadow: theme.shadows[2], mb: 4 }}>
        <CardContent sx={{ p: 3 }}>
          {/* Header */}
          <Box sx={{ display: 'flex', alignItems: 'center', mb: 3 }}>
            <Avatar
              sx={{
                bgcolor: theme.palette.primary.light,
                color: theme.palette.primary.contrastText || '#fff',
                width: 48,
                height: 48,
                mr: 2,
              }}
            >
              <PeopleIcon />
            </Avatar>
            <Box>
              <Typography variant="h5" fontWeight="bold" color="text.primary">
                {currentManager ? `${currentManager.name}'s Team Performance` : 'Staff Performance Report'}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                {currentManager 
                  ? `Performance metrics of ${currentManager.name}'s direct subordinates` 
                  : 'Performance metrics of managers based on their teams'}
              </Typography>
            </Box>
          </Box>

          {/* Breadcrumb Navigation */}
          {breadcrumbs.length > 0 && (
            <Box sx={{ mb: 3, display: 'flex', alignItems: 'center', flexWrap: 'wrap' }}>
              <Button 
                variant="text" 
                size="small" 
                onClick={() => navigate('/sales/manager-performance')}
                sx={{ mr: 1 }}
              >
                Home
              </Button>
              {breadcrumbs.map((item, index) => (
                <React.Fragment key={item.id}>
                  <Typography variant="body2" color="text.secondary" sx={{ mx: 0.5 }}>
                    &gt;
                  </Typography>
                  {index === breadcrumbs.length - 1 ? (
                    <Typography variant="body2" color="primary" fontWeight="medium">
                      {item.name}
                    </Typography>
                  ) : (
                    <Button 
                      variant="text" 
                      size="small" 
                      onClick={() => handleNavigateUp(item)}
                      sx={{ mr: 1 }}
                    >
                      {item.name}
                    </Button>
                  )}
                </React.Fragment>
              ))}
            </Box>
          )}

          {/* Info box explaining drill-down functionality */}
          <Paper sx={{ p: 2, mb: 3, borderRadius: 2, bgcolor: theme.palette.info.light, color: theme.palette.info.contrastText }}>
            <Typography variant="body2" fontWeight={500}>
              <strong>How to use:</strong> Click on any manager row to drill down and see their subordinates' performance.
              {currentManager ? " Use the breadcrumb navigation above to go back up the hierarchy." : ""}
            </Typography>
          </Paper>

          {/* Filters */}
          <Paper sx={{ p: 2, mb: 4, borderRadius: 2 }}>
            <Grid container spacing={2} alignItems="center">
              <Grid item xs={12} md={4}>
                <TextField
                  label="Search Manager"
                  type="text"
                  size="small"
                  fullWidth
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <SearchIcon sx={{ color: theme.palette.primary.main }} />
                      </InputAdornment>
                    ),
                  }}
                  sx={{ '& .MuiOutlinedInput-root': { borderRadius: 2 } }}
                />
              </Grid>
              <Grid item xs={12} md={3}>
                <TextField
                  label="Start Date"
                  type="date"
                  size="small"
                  fullWidth
                  value={startDate}
                  onChange={(e) => setStartDate(e.target.value)}
                  InputLabelProps={{ shrink: true }}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <CalendarIcon sx={{ color: theme.palette.primary.main }} />
                      </InputAdornment>
                    ),
                  }}
                  sx={{ '& .MuiOutlinedInput-root': { borderRadius: 2 } }}
                />
              </Grid>
              <Grid item xs={12} md={3}>
                <TextField
                  label="End Date"
                  type="date"
                  size="small"
                  fullWidth
                  value={endDate}
                  onChange={(e) => setEndDate(e.target.value)}
                  InputLabelProps={{ shrink: true }}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <DateRangeIcon sx={{ color: theme.palette.primary.main }} />
                      </InputAdornment>
                    ),
                  }}
                  sx={{ '& .MuiOutlinedInput-root': { borderRadius: 2 } }}
                />
              </Grid>
              <Grid item xs={12} md={2}>
                <Box sx={{ display: 'flex', gap: 1 }}>
                  <Button
                    variant="contained"
                    onClick={generateReport}
                    disabled={loading}
                    startIcon={<FilterIcon />}
                    sx={{
                      borderRadius: '2rem',
                      bgcolor: theme.palette.primary.main,
                      '&:hover': { bgcolor: theme.palette.primary.dark }
                    }}
                  >
                    Apply Filters
                  </Button>
                  <Button
                    variant="outlined"
                    onClick={exportToCSV}
                    disabled={loading || reportData.length === 0}
                    startIcon={<FileDownloadIcon />}
                    sx={{
                      borderRadius: '2rem',
                      borderColor: theme.palette.primary.main,
                      color: theme.palette.primary.main,
                      '&:hover': { borderColor: theme.palette.primary.dark }
                    }}
                  >
                    Export CSV
                  </Button>
                </Box>
              </Grid>
            </Grid>
          </Paper>

          {/* Summary Cards */}
          <Box sx={{ mb: 4 }}>
            <Grid container spacing={3}>
              <Grid item xs={12} sm={6} md={3}>
                <Paper
                  elevation={0}
                  sx={{
                    p: 2,
                    borderRadius: 2,
                    bgcolor: theme.palette.primary.light,
                    color: theme.palette.primary.contrastText,
                    textAlign: 'center',
                    height: '100%',
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center'
                  }}
                >
                  <Typography variant="h3" sx={{ fontWeight: 700, mb: 1 }}>
                    {totalManagers}
                  </Typography>
                  <Typography variant="body1">Active Managers</Typography>
                </Paper>
              </Grid>
              <Grid item xs={12} sm={6} md={3}>
                <Paper
                  elevation={0}
                  sx={{
                    p: 2,
                    borderRadius: 2,
                    bgcolor: theme.palette.info.light,
                    color: theme.palette.info.contrastText,
                    textAlign: 'center',
                    height: '100%',
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center'
                  }}
                >
                  <Typography variant="h3" sx={{ fontWeight: 700, mb: 1 }}>
                    {totalMarketingStaff}
                  </Typography>
                  <Typography variant="body1">Marketing Staff</Typography>
                </Paper>
              </Grid>
              <Grid item xs={12} sm={6} md={3}>
                <Paper
                  elevation={0}
                  sx={{
                    p: 2,
                    borderRadius: 2,
                    bgcolor: theme.palette.warning.light,
                    color: theme.palette.warning.contrastText,
                    textAlign: 'center',
                    height: '100%',
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center'
                  }}
                >
                  <Typography variant="h3" sx={{ fontWeight: 700, mb: 1 }}>
                    {totalVisits}
                  </Typography>
                  <Typography variant="body1">Total Visits</Typography>
                </Paper>
              </Grid>
              <Grid item xs={12} sm={6} md={3}>
                <Paper
                  elevation={0}
                  sx={{
                    p: 2,
                    borderRadius: 2,
                    bgcolor: theme.palette.success.light,
                    color: theme.palette.success.contrastText,
                    textAlign: 'center',
                    height: '100%',
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center'
                  }}
                >
                  <Typography variant="h3" sx={{ fontWeight: 700, mb: 1 }}>
                    {totalSalesAmount.toFixed(2)}
                  </Typography>
                  <Typography variant="body1">Total Sales Amount</Typography>
                </Paper>
              </Grid>
            </Grid>
          </Box>

          {/* Results Table */}
          {loading ? (
            <Box sx={{ mb: 2 }}>
              <LinearProgress />
              {/* Skeleton placeholders for table */}
              <TableContainer>
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell sx={{ fontWeight: 'bold' }}>Manager/Staff</TableCell>
                      <TableCell sx={{ fontWeight: 'bold', textAlign: 'center' }}>Role</TableCell>
                      <TableCell sx={{ fontWeight: 'bold', textAlign: 'center' }}>Team Size</TableCell>
                      <TableCell sx={{ fontWeight: 'bold', textAlign: 'center' }}>Total Visits</TableCell>
                      <TableCell sx={{ fontWeight: 'bold', textAlign: 'center' }}>Retailers Reached</TableCell>
                      <TableCell sx={{ fontWeight: 'bold', textAlign: 'right' }}>Total Sales ()</TableCell>
                      <TableCell sx={{ fontWeight: 'bold', textAlign: 'center' }}>Actions</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {[...Array(5)].map((_, i) => (
                      <TableRow key={i}>
                        <TableCell>
                          <Box sx={{ display: 'flex', alignItems: 'center' }}>
                            <Skeleton variant="circular" width={36} height={36} sx={{ mr: 1.5 }} />
                            <Skeleton variant="text" width={120} />
                          </Box>
                        </TableCell>
                        <TableCell align="center"><Skeleton variant="text" width={80} /></TableCell>
                        <TableCell align="center"><Skeleton variant="text" width={60} /></TableCell>
                        <TableCell align="center"><Skeleton variant="text" width={60} /></TableCell>
                        <TableCell align="center"><Skeleton variant="text" width={60} /></TableCell>
                        <TableCell align="right"><Skeleton variant="text" width={80} /></TableCell>
                        <TableCell align="center"><Skeleton variant="rounded" width={80} height={24} /></TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </TableContainer>
            </Box>
          ) : reportData.length === 0 ? (
            <Box sx={{ textAlign: 'center', p: 4, bgcolor: 'rgba(0,0,0,0.02)', borderRadius: 2 }}>
              <Typography variant="body1" color="text.secondary">
                No staff performance data found for the selected criteria
              </Typography>
            </Box>
          ) : (
            <TableContainer>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell sx={{ fontWeight: 'bold' }}>Manager/Staff</TableCell>
                    <TableCell sx={{ fontWeight: 'bold', textAlign: 'center' }}>Role</TableCell>
                    <TableCell sx={{ fontWeight: 'bold', textAlign: 'center' }}>Team Size</TableCell>
                    <TableCell sx={{ fontWeight: 'bold', textAlign: 'center' }}>Total Visits</TableCell>
                    <TableCell sx={{ fontWeight: 'bold', textAlign: 'center' }}>Retailers Reached</TableCell>
                    <TableCell sx={{ fontWeight: 'bold', textAlign: 'right' }}>Total Sales ()</TableCell>
                    <TableCell sx={{ fontWeight: 'bold', textAlign: 'center' }}>Efficiency</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {displayedRows.length === 0 ? (
                    <TableRow>
                      <TableCell colSpan={7} align="center">
                        <Typography variant="body2" color="text.secondary">
                          No matching results found
                        </Typography>
                      </TableCell>
                    </TableRow>
                  ) : (
                    displayedRows.map((manager) => (
                      <TableRow 
                        key={manager.managerId}
                        sx={{ '&:hover': { bgcolor: 'rgba(0,0,0,0.04)' } }}
                        onClick={() => handleDrillDown(manager)}
                        style={{ cursor: 'pointer' }}
                      >
                        <TableCell>
                          <Box sx={{ display: 'flex', alignItems: 'center' }}>
                            <Avatar 
                              sx={{ 
                                width: 36, height: 36, mr: 1.5,
                                bgcolor: getRoleColor(manager.managerRole || manager.role) 
                              }}
                            >
                              {manager.managerName?.charAt(0) || '?'}
                            </Avatar>
                            <Box>
                              <Typography variant="body2" fontWeight={500}>
                                {manager.managerName}
                              </Typography>
                              {manager.hasSubordinates && (
                                <Typography variant="caption" color="text.secondary">
                                  Click to view subordinates
                                </Typography>
                              )}
                            </Box>
                          </Box>
                        </TableCell>
                        <TableCell align="center">
                          <Chip 
                            label={manager.managerRole || manager.role} 
                            size="small"
                            sx={{ 
                              borderRadius: '14px', 
                              fontWeight: 500,
                              bgcolor: `${getRoleColor(manager.managerRole || manager.role)}20`,
                              color: getRoleColor(manager.managerRole || manager.role)
                            }}
                          />
                        </TableCell>
                        <TableCell align="center">
                          <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                            <TeamIcon sx={{ color: theme.palette.info.main, fontSize: 16, mr: 0.5 }} />
                            <Typography variant="body2" fontWeight={500}>
                              {manager.teamSize}
                            </Typography>
                          </Box>
                        </TableCell>
                        <TableCell align="center">
                          <Typography variant="body2" fontWeight={500}>
                            {manager.totalVisits}
                          </Typography>
                        </TableCell>
                        <TableCell align="center">
                          <Typography variant="body2" fontWeight={500}>
                            {manager.uniqueRetailers}
                          </Typography>
                        </TableCell>
                        <TableCell align="right">
                          <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'flex-end' }}>
                            <MoneyIcon sx={{ color: theme.palette.success.main, fontSize: 18, mr: 0.5 }} />
                            <Typography variant="body2" fontWeight={500}>
                              {manager.totalSalesAmount.toFixed(2)}
                            </Typography>
                          </Box>
                        </TableCell>
                        <TableCell align="center">
                          <Tooltip 
                            title={
                              <Box>
                                <Typography variant="caption" fontWeight={600}>Efficiency Metrics:</Typography>
                                <Box sx={{ mt: 0.5 }}>
                                  <Typography variant="caption"> {manager.visitsPerStaff} visits per staff</Typography>
                                </Box>
                                <Box>
                                  <Typography variant="caption"> {manager.salesPerStaff} sales per staff</Typography>
                                </Box>
                                <Box>
                                  <Typography variant="caption"> {manager.salesPerVisit} sales per visit</Typography>
                                </Box>
                              </Box>
                            } 
                            arrow
                          >
                            <Chip 
                              icon={<EfficiencyIcon sx={{ fontSize: 16 }} />}
                              label={`${manager.salesPerStaff}`}
                              color={parseFloat(manager.salesPerStaff) > 1000 ? "success" : "default"}
                              size="small"
                              sx={{ 
                                borderRadius: '14px', 
                                fontWeight: 500,
                                minWidth: '80px'
                              }}
                            />
                          </Tooltip>
                        </TableCell>
                      </TableRow>
                    ))
                  )}
                </TableBody>
              </Table>
              <TablePagination
                rowsPerPageOptions={[10, 25, 50]}
                component="div"
                count={filteredData.length}
                rowsPerPage={rowsPerPage}
                page={page}
                onPageChange={handleChangePage}
                onRowsPerPageChange={handleChangeRowsPerPage}
              />
            </TableContainer>
          )}
        </CardContent>
      </Card>

      {/* Snackbar for notifications */}
      <Snackbar
        open={snackbar.open}
        autoHideDuration={6000}
        onClose={() => setSnackbar({ ...snackbar, open: false })}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert 
          onClose={() => setSnackbar({ ...snackbar, open: false })} 
          severity={snackbar.severity}
          sx={{ width: '100%' }}
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </Box>
  );
} 